.App {
  text-align: center;
}

.App-logo {
  animation: App-logo-spin infinite 20s linear;
  height: 80px;
}

.App-header {
  background-color: #222;
  height: 180px;
  padding: 20px;
  color: white;
}

.App-intro {
  font-size: large;
  margin: 0;
  padding: 40px 20px 20px;
}

.step-text {
    padding: 20px;
    background: cornsilk;
    margin: 10px 20px 30px;
    border-radius: 8px;
  }

@keyframes App-logo-spin {
  from { transform: rotate(0deg); }
  to { transform: rotate(360deg); }
}
